.header{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding:20px; 
  position:sticky;
  top:0;
  z-index: 100;
  background-color:white;
}
.header_logo{
  height: 25px;
  object-fit:contain;
  margin-left:20px;
}
.header_mid{
  display: flex;
  align-items: center;
  width:40%;
  border: 1px solid black;
}
.header_searchButton{
  width:50px !important;
  background-color: #fafafa;
  border-left: 1px solid lightgrey;
  color: black;

}


.header_right{
  display: flex;
  align-items: center;
}

.header_icon{
  margin-right:8px;
}


.header_mid > input{
  flex:1;
  border: none;
}